export default "import React from 'react';\nimport Grid from 'terra-grid';\n\nimport Aggregator from 'terra-aggregator';\nimport AggregatorItem from 'terra-aggregator/lib/terra-dev-site/doc/common/AggregatorItem';\n\nconst items = Object.freeze([{\n  key: 'SECTION_0',\n  component: <AggregatorItem name=\"Section 0\" />,\n}, {\n  key: 'SECTION_1',\n  component: <AggregatorItem name=\"Section 1\" />,\n}, {\n  key: 'SECTION_2',\n  component: <AggregatorItem name=\"Section 2\" />,\n}, {\n  key: 'SECTION_3',\n  component: <AggregatorItem name=\"Section 3\" />,\n}]);\n\nconst CustomRenderAggregatorExample = () => (\n  <div>\n    <Aggregator\n      items={items}\n      render={renderData => (\n        <Grid>\n          <Grid.Row>\n            <Grid.Column tiny={6}>\n              {renderData.items[0]}\n            </Grid.Column>\n            <Grid.Column tiny={6}>\n              {renderData.items[1]}\n            </Grid.Column>\n          </Grid.Row>\n          <Grid.Row>\n            <Grid.Column tiny={6}>\n              {renderData.items[2]}\n            </Grid.Column>\n            <Grid.Column tiny={6}>\n              {renderData.items[3]}\n            </Grid.Column>\n          </Grid.Row>\n        </Grid>\n      )}\n    />\n  </div>\n);\n\nexport default CustomRenderAggregatorExample;\n"